import React from 'react';
import {ClassroomType, QueuePolicyTypes} from "../../../../models/models";
import styles from './browseClassroomPopupBody.module.css';

interface PropTypes {
  classroom: ClassroomType;
}

const BrowseClassroomPopupBody: React.FC<PropTypes> = ({classroom}) => {
  return (
    <div className={styles.list}>
      <div><span>ID: </span><span>{classroom.id}</span></div>
      <div><span>: </span><span>{classroom.name}</span></div>
      <div><span>: </span><span>{classroom.description}</span></div>
      <div><span>: </span><span>{classroom.floor}</span></div>
      <div><span>: </span><span>{classroom.chair ? classroom.chair.name : '-'}</span></div>
      <div><span>: </span><span>{classroom.special ? '' : ''}</span></div>
      <div><span>: </span><span>{classroom.isWing ? '' : ''}</span></div>
      <div><span> : </span><span>{classroom.isOperaStudio ? '' : ''}</span></div>
      <div><span>: </span><span>{classroom.isHidden ? '' : ''}</span></div>
      {classroom.queueInfo.queuePolicy.policy === QueuePolicyTypes.SELECTED_DEPARTMENTS && (
        <div>
          <span>  , : </span>
          <span>
         {classroom.queueInfo.queuePolicy.queueAllowedDepartments.map(({department}) => {
           return department.name
         }).join(', ')}
        </span>
        </div>
      )}
      <div><span>: </span>{classroom.instruments?.length > 0 ? <ul className={styles.instruments}>
        {classroom.instruments.map(item => <li key={item.id}>{item.name}</li>)}
      </ul> : '-'}</div>
    </div>
  );
}

export default BrowseClassroomPopupBody;